@import '../../_variables.scss';

.about-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
    font-family: Arial, sans-serif;

}

.about-content {
    display: flex;
    gap: 2rem;
    margin-bottom: 2rem;
}

.profile-image {
    flex: 1;

    img {
        width: 100%;
        height: auto;
        border-radius: 2rem;
        object-fit: cover;
    }
}

.about-text {
    flex: 1;

    h3 {
        color: $primary-color;
        margin-bottom: 1rem;
        @include H3;
    }

    p {
        @include caption3;
    }
}

.resume-button {
    @include caption3;
}

.skills-title {
    display: flex;
    justify-content: center;

    h2 {
        color: $primary-color;
        @include Body2;
    }
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}

.skill-item {
    background-color: #fff;
    padding: 16px 50px 16px 50px;
    gap: 10px;
    border-radius: 75px;
    display: flex;
    align-items: center;
    border: 2px solid #1f1f1f;
    @include caption1;
}

.skill-icon {
    font-size: 1.2rem;
}

@media (max-width: 768px) {
    .about-content {
        flex-direction: column;
    }

    .skills-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

.exp-and-work {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
    display: flex;
    justify-content: space-between;

    .section {
        width: 48%;

        h4 {
            @include H4;
            color: $primary-color;
            display: flex;
            align-items: center;
            gap: 10px;
        }

        .item {
            margin-bottom: 30px;
            padding-bottom: 8px;
            border-bottom: 1px solid;

            .title {
                @include Body1;
                margin: 16px 0 16px 0;
            }

            .date-company {
                display: flex;
                justify-content: space-between;
                font-size: 14px;

                .date {
                    padding: 3px 20px 3px 20px;
                    gap: 10px;
                    border-radius: 21px;
                    border: 1px solid #1f1f1f;
                }

                .company {
                    color: #8C8C8C;
                    @include caption3_bold;
                }
            }
        }
    }
}

// Add media query for responsiveness
@media (max-width: 768px) {
    .exp-and-work {
        flex-direction: column;

        .section {
            width: 100%;
            margin-bottom: 40px;
        }
    }
}