@import '../../_variables.scss';

.contact-section {

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 120px;
  position: relative;

  background-color: $background-color;

  .title {
    @include H2;
    color: $primary-color;
    margin: 0px;
  }

  .description {
    @include Body2;
    color: $primary-color;
    max-width: 945px;
  }

  .email-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;

    .arrow-down {
      color: $primary-color;
      padding-bottom: 40px;
    }

    .email {
      @include Title;
      color: #1F1F1F;
      text-decoration: none;
    }

    .social-icons {
      display: flex;
      gap: 1rem;
      margin-bottom: 2rem;

      .icon {
        width: 24px;
        height: 24px;
        color: #3B82F6;
      }
    }

    .leaf-svg {
      position: absolute;
      bottom: 20px;
      left: 20px;
      width: 100px;
      height: 100px;
    }

    .lightning-svg {
      position: absolute;
      top: 20px;
      right: 20px;
      width: 50px;
      height: 50px;
    }

    .footer-text {
      font-size: 0.875rem;
      color: #6B7280;
    }
  }
}