@import '../../_variables.scss';

.introduction-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.introduction {
  position: relative;
  width: 100%;
  max-width: 1330px;
  height: 600px;
  padding: 60px 80px;
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;

  .content {
    position: relative;
    z-index: 1;
    width: 706px;
  }

  .hey-there {
    margin: 0px;
    color: $primary-color;
    @include H4;
  }

  .name {
    margin: 0px;
    @include H1_bold;
    color: $primary-color;
  }

  .description {
    margin: 0px;
    color: #1F1F1F;
    @include Body1;
  }

  .decorations {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;

    .star-1 {
      position: absolute;
      top: 140px;
      left: 127px;
      width: 71px;
      height: 103px;
    }

    .star-2 {
      position: absolute;
      top: 70px;
      left: 170px;
      width: 43px;
      height: 65px;
    }

    .circle {
      position: absolute;
      top: 60px;
      left: 970px;
      width: 195px;
      height: 181px;
    }

    .saas {
      position: absolute;
      top: 330px;
      left: 0px;
      width: 273px;
      height: 182px;
    }

    .non-code {
      position: absolute;
      top: 200px;
      right: 0px;
      width: 273px;
      height: 182px;
    }
  }
}

@media (max-width: 1200px) {
  .introduction-wrapper {
    transform: scale(0.8);
  }
}

@media (max-width: 768px) {
  .introduction-wrapper {
    transform: scale(0.5);
  }
}