@import '../../_variables.scss';

.project-1-container {
  margin: 0 auto;
  background: #fff;
  @include caption3;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-1-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
  background: #C9F5FF;
  padding: 150px 100px 100px 100px;

  .header-content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 1;

    .header-title {
      @include H3;
      color: $primary-color;
    }

    .header-subtitle {
      @include caption3_bold;
    }
  }

  .header-image {
    flex: 1;
    text-align: right;
    max-width: 80%;


    img {
      max-width: 100%;
      height: auto;
    }
  }
}

.project-1-content {
  display: flex;
  flex-direction: column;
  padding-top: 80px;
  gap: 60px;
  max-width: 1200px;
  padding-bottom: 120px;
}

.project-1-overview {
  padding-left: 120px;
  padding-right: 120px;

  .title {
    @include H5;
  }

  .overview-content {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;

    .overview-description {
      flex: 2;
      margin-right: 2rem;
    }

    .overview-details {
      flex: 1;

      .detail-title {
        @include caption1_bold;
      }

      .detail-item {
        margin-bottom: 1rem;
      }
    }
  }

  .project-goal {
    h2 {
      font-size: 2rem;
      margin-bottom: 1rem;
    }
  }
}

.project-1-process {
  background: #F7F8FA;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-top: 36px;
  padding-bottom: 36px;

  img {
    max-width: 800px;
    height: auto;
    display: block;
    width: 100%;
  }

  .title {
    @include H4;
    color: $primary-color;
    text-align: center;
  }
}

.project-1-details {
  padding-left: 120px;
  padding-right: 120px;

  .discovery-title {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 2rem;

    .discovery-icon-container {
      display: flex;
      gap: 1rem;

      .discovery-icon {
        width: 40px;
        height: 40px;
        margin-bottom: 1rem;
      }

      .discovery-title-text {
        @include H5;
        color: $primary-color;
      }
    }

  }

  .title {
    margin-top: 2rem;
    margin-bottom: 1rem;
    @include caption1_bold;
  }

  .problems-list {
    @include caption3;
  }

  .screenshot-container {
    max-width: 100%;
    overflow: hidden;

    .job-search-screenshot {
      margin-top: 2rem;
      width: 100%;
      height: auto;
      display: block;
    }
  }

  .full-full-button {
    display: flex;
    justify-content: center;
    padding-top: 2rem;
  }
}

.all-pages-container {
  background: #E7FBFF;
  padding-top: 0px;
}

.learnings-container {
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 120px;
  padding-right: 120px;
  background: #C9F5FF;
  
  .title {
    @include H5;
    padding-bottom: 2rem;
  }

  .takeaways-content {
    @include caption3;
  }
}

@media (max-width: 768px) {
  .project-1-header {
    flex-direction: column;
    text-align: center;

    .header-content,
    .header-image {
      width: 100%;
    }

    .header-image {
      margin-top: 30px;
    }
  }
}